@import "variables.scss";

#welcome {
  background-color: $dark-gray;
  display: flex;
  justify-content: center;
  margin-top: -80px;
  padding: calc(5rem + 170px) 0 0;

  .content {
    align-items: center;

    h1.typewriter {
      font-weight: 500;
      max-width: 960px;
      width: 960px;
    }

    p {
      margin-bottom: 5rem;
      max-width: 60vw;
      text-align: center;
    }

    .cta {
      align-items: center;
      display: flex;
    }
  }

  a.pill {
    margin: 2rem;
  }

  .waves {
    margin-top: 6rem;
  }
}

@media (max-width: 1000px) {
  #welcome .content h1.typewriter {
    font-size: 36px;
    max-width: 725px;
    width: 725px;
  }
}

@media (max-width: 800px) {
  #welcome .content h1.typewriter {
    font-size: 34px;
    max-width: 685px;
    width: 685px;
  }
}

@media (max-width: 700px) {
  #welcome .content {
    h1.typewriter {
      font-size: 28px;
      max-width: 565px;
      width: 565px;
    }

    p {
      max-width: 80vw;
    }
  }
}

@media (max-width: 600px) {
  #welcome .content {
    h1.typewriter {
      font-size: 24px;
      max-width: 485px;
      width: 485px;
    }

    .cta {
      flex-direction: column;
    }
  }
}

@media (max-width: 500px) {
  #welcome .content h1.typewriter {
    font-size: 18px;
    max-width: 365px;
    width: 365px;
  }
}

@media (max-width: 400px) {
  #welcome .content h1.typewriter {
    font-size: 16px;
    max-width: 325px;
    width: 325px;
  }
}
